.Mainpage h1 {
  color: #fff;
  font-size: 40px;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 0 15px, 0 0 20px #0073e6, 0 0 25px #0073e6, 0 0 30px #0073e6,
    0 0 35px #0073e6;
}
.container {
  width: 100%;
  height: 100vh;
  max-width: 1600px;
  user-select: none;
  box-shadow: 0px 0px 3px 3px lightslategray;
  margin: 0 auto;
}
.slideshow-container {
  width: 100%;
  height: 90%;
  background-color: lightgray;
  position: relative;
}
.prev {
  font-size: 30px;
  color: #fff;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px, 0 0 20px #0073e6,
    0 0 25px #0073e6, 0 0 30px #0073e6, 0 0 35px #0073e6;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  position: absolute;
  width: 50px;
  background-color: #00000036;
  transition: background-color 0.5s;
  height: 72px;
  top: 50%;
  transform: translateY(-50%);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.prev:hover,
.next:hover {
  background-color: #00000098;
  cursor: pointer;
  font-size: 31px;
}
.next {
  font-size: 30px;
  color: #fff;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px, 0 0 20px #0073e6,
    0 0 25px #0073e6, 0 0 30px #0073e6, 0 0 35px #0073e6;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  position: absolute;
  width: 50px;
  background-color: #00000036;
  transition: background-color 0.5s;
  height: 72px;
  top: 50%;
  left: auto;
  right: 0;
  transform: translateY(-50%);
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
}
@media only screen and (max-width: 500px) {
  .App h1 {
    font-size: 10;
  }
  .prev,
  .next {
    font-size: 10px;
  }
}
